import React, { Component, Fragment } from 'react';
import logo from './logo.svg';
import './App.css';

require('dotenv').config()

const algoliasearch = require('algoliasearch');
const algoliasearchHelper = require('algoliasearch-helper');


const client = algoliasearch(process.env.REACT_APP_ALGOLIA_APP_ID, process.env.REACT_APP_ALGOLIA_API_KEY);
const helper = algoliasearchHelper(client, process.env.REACT_APP_ALGOLIA_INDEX_NAME, {
  facets: ['food_type']
});


const cachedResults = {

}


const ResultRow = ({ result }) => {
  return (
    <div className="row row-eq-height p-3 text-left">
      <div className="col-3">
        <img className="rounded" src={result.image_url} height="auto" width="150" />
      </div>
      <div className="col-9">

        <div style={{height:"calc(90% / 3)"}} className="h5"><strong>{result.name}</strong></div>
        <div style={{height:"calc(90% / 3)"}} className="">
          <span className="text-warning">
            {result.stars_count}
          </span>
          <span className="text-secondary">
            &nbsp;({result.reviews_count} reviews)
          </span>
        </div>
        <div style={{height:"calc(90% / 3)"}} className="text-secondary">{result.food_type} | {result.neighborhood} | {result.price_range}</div>
      </div>

    </div>
  )
}

const FoodTypeFilter = ({ filter, number }) => {
  return (
    <div>
      {filter} ({number})
    </div>
  )
}


const getFoodTypesFromFacets = (facets) => {
  if(!facets.length) return []
  return facets.filter(facet => facet.name === 'food_type')[0].data
}

class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      hits: [],
      filters: {},
      searchInput: ''
    }
  }
  componentDidMount() {
    helper.on('result', (data) => {
      const { hits, query, facets } = data;
      cachedResults[query] = data;
      const filters = getFoodTypesFromFacets(facets)
      this.setState({
        hits: hits,
        filters,
        results: data
      })
    });
  }
  handleChange = (e) => {
    const searchInput = e.target.value;
    this.setState({ searchInput })
    clearTimeout(this.fetchResultsTimeout)
    this.fetchResultsTimeout = setTimeout(() => {
      this.fetchResults();
    }, 350)
  }
  fetchResults = () => {
    const searchInput = this.state.searchInput;
    if(!searchInput) return;
    if(cachedResults[searchInput]){
      this.setState({
        hits: cachedResults[searchInput].hits,
        filters: getFoodTypesFromFacets(cachedResults[searchInput].facets)
      })
    }else{
      console.log(`fetching: ${this.state.searchInput}`)
      helper
      .setQuery(searchInput)
      .search()
    }

  }

  getResultText = () => {
    if(!this.state.hits.length || !this.state.searchInput) return false
    const searchInput = this.state.searchInput;
    return (<Fragment><strong>{this.state.hits.length} results found </strong> in {this.state.results.processingTimeMS / 1000} seconds</Fragment>)
  }
  render() {

    return (
      <div className="App">
        <nav className="navbar navbar-dark fixed-top bg-dark flex-md-nowrap p-0 shadow">
          <a className="navbar-brand col-md-2 mr-0" href="#">Algolia Challenge</a>
          <input
            className="form-control form-control-dark w-100"
            type="text"
            placeholder="Search"
            value={this.state.searchInput}
            onChange={this.handleChange}
            aria-label="Search"/>
        </nav>
        <div className="container-fluid">
          <div className="row">
            <nav className="col-md-2 d-none d-md-block bg-light sidebar">
              <div className="sidebar-sticky">
                {Object.keys(this.state.filters).map(filter => {
                  const number = this.state.filters[filter]
                  return (<FoodTypeFilter
                    key={`filter-${filter}`}
                    filter={filter}
                    number={number} />)
                })
                }
              </div>
            </nav>
            <main role="main" className="col-md-9 ml-sm-auto col-lg-10 px-4">
              <div className="p-3">
                {this.getResultText()}
              </div>
              {this.state.hits.map(result => <ResultRow key={`${result.objectID}`} result={result}/>)}
            </main>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



